.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
  height: 100vh;
}

.App > div {
  background-color: #eee;
  width: 300px;
}

.user {
  text-align: center;
}

.user button {
  background-color: blue;
  color: white;
  outline: none;
  border: none;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  padding: 10px 20px;
  cursor: pointer;
}

.user button:nth-of-type(2) {
  background-color: rgb(219, 67, 67);
  margin: 0 2px;
}